a {
    text-decoration: none;
    color: black;
}
a:hover {
    color: gray;
}
.logo {
    display: inline-block;
    background: skyblue;
    margin-top: 10px;
    margin-left: 100px;
    margin-bottom: 20px;
}
.Toolbar {
    display: flex;
    margin-left: auto;
    margin-right: 100px;
}
.Toolbar_content {
    margin-top: 20px;
    margin-left: 20px;
}
.Toolbar_content_icon {
    display: flex;
    justify-content: center;
}
.Toolbar_content_icon:nth-child(1) {
    margin-right: 6px;
}
.Toolbar_text {
    justify-content: center;
    text-decoration: none;
    font-weight: 700;
}
.Toolbar_content img {
    display: block;
    width: 50px;
}
